.header {
  @media screen and (min-width: 1360px) {
    padding: 0 calc((100vw - 1400px)/2);
  }
  &:before {
    content: '';
    position:absolute;
    height: 150px;
    width: 100%;
    z-index: -1;
    left: 0;
    background: -webkit-linear-gradient(top,rgb(0, 0, 0),rgba(0, 0, 0, 0)); /*Safari 5.1-6*/
    background: -o-linear-gradient(top,rgb(0, 0, 0),rgba(0, 0, 0, 0)); /*Opera 11.1-12*/
    background: -moz-linear-gradient(top,rgb(0, 0, 0),rgba(0, 0, 0, 0)); /*Fx 3.6-15*/
    background: linear-gradient(to bottom, rgb(0, 0, 0), rgba(0, 0, 0, 0)); /*Standard*/
    transition: height 0.5s;
  }
  @media screen and (max-width: 768px) {
    &.active {
      *{
        z-index: 3;
      }
      &:before {
        z-index: 2;
        height: 500px;
      }
    }
  }
@media screen and (max-width: 768px) {
  flex-direction: row;
}
  &__logo {
  img {
      @media screen and (max-width: 1024px) {
        max-width: 100px;
      }
      @media screen and (max-width: 768px) {
        max-width: 80px;
      }
    }
    .languageSwitcher{
      display:none;
      @media screen and (max-width: 768px) {
        display:flex;
        margin: -10px 20px;
        li {
          list-style: none;
          opacity: 0.6;
          &.current-lang{
            opacity: 1;
          }
        }
      }
    }
  }
  &__burger {
    display: none;
    float: left;
    height: 35px;
    margin: 22px 10px 0 15px;
    position: relative;
    width: 35px;
    z-index: 10;
    @media screen and (max-width: 768px) {
      display: block;
      span {
        background-color: #fff;
        border-radius: 2px;
        height: 4px;
        opacity: 1;
        position: absolute;
        -webkit-transition: .25s ease-in-out;
        -moz-transition: .25s ease-in-out;
        -o-transition: .25s ease-in-out;
        transition: .25s ease-in-out;
        width: 100%;
        top: 9px;
        &:nth-child(2) {
          top: 18px;
        }
        &:nth-child(3) {
          top: 27px;
        }
      }
      &.active {
        z-index: 11;
        span {
          &:nth-child(1) {
            transform: rotate(135deg);
            top: 18px;
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:nth-child(3) {
            transform: rotate(-135deg);
            top: 18px;
          }
        }
      }
    }
  }
  &__restaurants {
    margin-right:auto;
    flex-direction: column;
    justify-content: center;
    @media screen and (max-width: 1024px) {
      max-width: 320px;
      max-height: 80px;
    }
    @media screen and (max-width: 768px) {
      max-width: 185px;
      margin-left: -10px;
    }
    & div {
      transition: all 0.5s;
      color:#fff;
      font-size: 24px;
      opacity: 0.8;
      padding: 5px 10px;
      @media screen and (max-width: 768px) {
        font-size: 14px;
        padding: 2px 4px;
      }
      &:first-child {
          border-bottom:solid 2px;
      }
      &.active {
        opacity: 1;
        font-size: 26px;
        @media screen and (max-width: 768px) {
          font-size: 16px;
        }
        // &:before {
        //   content: "";
        //   padding-right: 10px;
        //   background: url("/wp-content/themes/veladora/images/avocado.png") no-repeat;
        //   background-size: 30px;
        //   height: 30px;
        //   width: 30px;
        //   display: inline-block;
        //   @media screen and (max-width: 768px) {
        //     background-size: 20px;
        //     height: 20px;
        //     width: 20px;
        //     padding-right: 2px;
        //   }
        // }
      }
      &:hover {
        // cursor: url('/wp-content/themes/veladora/images/cursor.cur'), pointer;
        cursor: pointer;
        opacity: 1;
      }
    }
  }
  &__menu {
    padding-top: 10px;
    min-width: 650px;
    justify-content: space-around;
    @media screen and (max-width: 1024px) {
      min-width: 550px;
    }
    @media screen and (max-width: 768px) {
      display: none;
      &.active {
        z-index: 10;
        display: block;
        position: absolute;
        right: 10px;
        max-width: 300px;
        min-width: auto;
        // top: 3px;
        top: 70px;
      }
    }
    a {
      font-size: 28px;;
    }
    ul{
      display: flex;
      flex-wrap: wrap;
      @media screen and (max-width: 1024px) {
        padding: 0;
      }
      @media(max-width:768px){
        max-width: 135px;
        margin: 0;
        padding: 0;
        margin-right: 50px;
      }
      li {
        margin-left:auto;
        margin-right:20px;
        list-style:none;
        display: inline-block;
        @media(max-width:768px){
          text-align:left;
          margin-left: 0;
        }
        &.current-menu-item a,
        &:hover a {
            color: #7de33a;
        }
        &.lang-item {
          opacity: 0.6;
          transition: all 0.5s;
          padding-right: 5px;
          margin: 0;
          @media(max-width:768px){
            display: none;
          }
          &:hover,
          &.current-lang{
            opacity: 1;
          }
        }
      }
    }
  }
}
