.menu_page {
  color:#fff;
  text-align: center;
  flex-direction: column;
  background: url(/wp-content/themes/veladora/images/decor.png)  no-repeat;
  background-position: 10% 30%;
  @media screen and (max-width: 768px) {
    // background:none;
  }
  p {
    font-size: 30px;
    margin-top: 10px;
    margin-bottom: 10px;
    line-height: 1em;
    @media screen and (max-width: 768px) {
      font-size: 20px;
    }
  }
  h2 {
    font-size: 42px;
    line-height: 20px;
    @media screen and (max-width: 768px) {
      font-size: 28px;
    }
  }
}
