.main_container{
  min-height:calc(90vh - 200px);
  position: relative;
  @media screen and (min-width: 1360px) {
    padding: 0 calc((100vw - 1400px)/2);
  }
  @media screen and (max-width: 768px) {
    min-height:auto;
  }
  @media screen and (min-height: 800px) and (min-width: 769px){
    min-height:700px;
  }
  &:before{
    content: '';
    position:absolute;
    height: 500px;
    width: 100%;
    z-index: -1;
    left: 0;
    background: -webkit-linear-gradient(bottom,rgb(0, 0, 0),rgba(0, 0, 0, 0)); /*Safari 5.1-6*/
    background: -o-linear-gradient(bottom,rgb(0, 0, 0),rgba(0, 0, 0, 0)); /*Opera 11.1-12*/
    background: -moz-linear-gradient(bottom,rgb(0, 0, 0),rgba(0, 0, 0, 0)); /*Fx 3.6-15*/
    background: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0)); /*Standard*/
    bottom:0;
  }
}
.headline{
  padding: calc(100vh - 500px) 50px 50px;
  min-height: 250px;
  flex-wrap: nowrap;
  flex-direction: column;
  max-width: calc( 50% - 100px );
  &.h2{
    min-height: auto;
  }
  @media screen and (max-width: 768px) {
    justify-content: flex-end;
    min-height: 250px;
    max-width: calc(100vw - 60px);
    margin: 0 auto;
    padding: 100px 30px 0;
    margin-top: calc(100vw - 250px);
  }
  ._cantina,
  ._tacos{
    opacity:0;
    height: 0;
    transition: opacity 1s;
  }
  .h1{
    max-width: 200px;
  }
}
.information{
  padding: calc(100vh - 450px) 50px 50px;
  max-width: calc( 50% - 100px );
  @media screen and (max-width: 768px) {
    padding: 0;
    max-width: 100%;
    margin: 0 30px 10px;
  }
  &__contacts{
    padding-right: 40px;
    @media screen and (max-width: 768px) {
      padding-right: 20px;
    }
  }
  &__address{
    font-size: 32px;
    @media screen and (max-width: 768px) {
      line-height: 1;
      margin: 0;
    }
  }
  &__social{
      padding: 20px 20px 50px 0;
      &.sub_ {
        display:none;
      }
      @media screen and (max-width: 768px) {
        display:none;
        &.sub_ {
          display:block;
          text-align: center;
          padding: 20px;
        }
      }
      img {
        padding-right: 15px;
        height: 50px;
      }
  }
  &__schedule {
    padding-top: 5px;
    p {
      font-size: 30px;
      line-height: 1;
      text-align: right;
      @media screen and (max-width: 768px) {
        font-size: 20px;
        text-align: left;
        &:not(.information__link){
          margin: 10px 0;
        }
      }
      a{
        text-transform: none;
        span {
            font-size: 38px;
            border-bottom: solid 1px;
            @media screen and (max-width: 768px) {
              font-size: 22px;
            }
        }
      }
    }
  }
  &__link{
    text-align: right;
    @media screen and (max-width: 768px) {
      display: none;
      margin: 20px 0;
    }
    & img{
      vertical-align: sub;
      height: 40px;
      @media screen and (max-width: 768px) {
        height: 30px;
      }
    }
    &._cantina{
      opacity: 0;
      height: 0;
    }
    &._menu{
      @media screen and (max-width: 768px) {
        display:none;
      }
    }
    // &._headBooking{
    //   display: none;
    //   visibility: hidden;
    //   @media(max-width:768px){
    //     visibility: visible;
    //     opacity:0;
    //     position: absolute;
    //     top: 50px;
    //     right: 10px;
    //     font-size: 16px;
    //     text-decoration: underline;
    //     img{
    //       height: 25px;
    //     }
    //   }
    // }
  }
  .book{
    background: none;
    form{
      padding: 0;
      .h2{
        font-size: 22px;
        text-align: left;
      }
      .date_inputs{
        display: block;
      }
      input{
        border-color: #fff;
        max-width: 60vw;
        &.button{
          margin: 10px 0 50px;
        }
      }
    }
  }
}
