.content{
  justify-content: center;
  padding: 0 150px 50px;
  width:auto;
  background: black;
  @media(max-width: 768px){
    padding: 0 30px;
    max-width: calc(100vw - 60px);
  }
  p{
    max-width: 800px;
    @media screen and (min-width: 1360px) {
      max-width: none;
      margin: 0 calc((100vw - 1400px)/2);
    }
    img{
      @media(max-width: 768px){
        max-width: 100%;
        height: auto;
      }
    }
  }
  &.team_cards{
    // background: url(/wp-content/themes/veladora/images/decor.png)  no-repeat;
    margin-top: 100px;
  }
  .team_card{
    width: 100%;
    margin-bottom: 100px;
    &:nth-child(2n){
      text-align: right;
      @media(max-width: 768px){
        text-align: left;
      }
    }
    &__image img{
      max-width: 500px;
      @media(max-width: 768px){
        max-width: calc(100vw - 60px);
      }
    }
    &__paragraph{
      p{
        font-size: 32px;
        margin-top: -250px;
        margin-left: 150px;
        max-width: 800px;
        padding: 50px 250px;
        @media(max-width: 768px){
          margin:0;
          padding:0;
          max-width: calc(100vw - 60px);
        }
      }
    }
  }
}
.location{
  max-width: none;
  justify-content: center;
  @media(max-width: 768px){
    max-width: calc(100vw - 60px);
  }
  p{
    max-width: none;
  }
  &_information{
    flex-direction: column;
    @media (min-width: 769px) and (max-width: 1024px){
      max-width: 250px;
    }
  }
  .map{
    padding-top: 20px;
    @media(max-width: 768px){
      margin: 0;
    }
  }
  &.directions{
    padding: 0;
  }
  &.headline{
    padding: 50px 0 0;
    width: 100%;
    @media(max-width: 768px){
      padding: 50px 30px 0;
    }
  }
  .information__social{
    padding-right: 0;
    text-align: right;
  }
}
.book{
  justify-content: center;
  width: 100%;
  margin: 50px 0 0;
  display: none;
  @media(max-width: 768px){
    margin: 0;
    background: rgba(0, 0, 0, 0.3);
  }
  form{
    max-width: 600px;
    padding: 20px;
    margin: 0 auto;
    .h2{
      text-align: center;
    }
  }
  input {
    border: none;
    border-bottom: solid 1px green;
    background: transparent;
    padding-left: 10px;
    width: 600px;
    .ajax-loader{
      display: none;
    }
    &:focus{
      outline:none;
    }
    @media(max-width: 768px){
      width: 100%;
      max-width: calc(100vw - 60px);
    }
    &.button {
      text-align: center;
      margin: 50px auto 0;
      border: solid 1px green;
      padding: 10px 20px;
      transition: all 0.5s;
      max-width: 250px;
      &:hover {
        background:rgba(0,0,0,0.3);
        cursor: url('/wp-content/themes/veladora/images/cursor.cur'), pointer;
      }
    }
  }
  .date_inputs {
    display: flex;
    width: 600px;
    @media(max-width: 768px) {
      width: 100%;
    }
    span, input {
      width:280px;
      @media(max-width: 768px) {
        width: 100%;
      }
      &.time {
        margin-left: auto;
      }
    }
  }
}
.ui-datepicker,
.ui-timepicker,
.ui-widget-header,
.ui-slider,
.ui-slider-handle,
.ui-datepicker button,
.ui-datepicker-header,
.ui-datepicker-calendar,
.ui-datepicker-calendar td>a {
    background: #fff!important
}
