.preloader{
      display: none;
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 20;
  color: #fff;
  font-size: 40px;
  transition: all 0.5s;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(/wp-content/themes/veladora/images/top_bg.jpg);
  @media(max-width: 768px){
    background-position: 50%;
  }
}

.place{
  transition: all 0.5s;
  display: flex;
  width: 50vw;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 0.6;
  @media(max-width: 768px){
    opacity: 1;
    width:auto;
    text-align: center;
    height: 50vh;
  }
}

.place:hover{
  text-decoration: underline;
  opacity: 1;
}
