.footer{
  // padding: 100px 0 30px;
  padding: 0 0 30px;
  background: url(/wp-content/themes/veladora/images/footer_bg.jpg) no-repeat #000;
  background-position: bottom;
  background-size: 100%;
  width: 100%;
  left: 0;
  bottom: 0;
  justify-content: center;
  position: relative;
  min-height: 350px;
  &.tacos{
    li a{
      color: #000;
    }
  }
  @media screen and (max-width: 768px) {
    padding: 0;
    background-position: bottom;
    background-size: 100% auto;;
    &.tacos{
      background-position: top;
      .footer__menu{
        li a{
          color: #fff;
        }
      }
    }
  }
  &:before{
    content: '';
    position:absolute;
    height: 130px;
    width: 100%;
    left: 0;
    background: -webkit-linear-gradient(top,rgb(0, 0, 0),rgba(0, 0, 0, 0)); /*Safari 5.1-6*/
    background: -o-linear-gradient(top,rgb(0, 0, 0),rgba(0, 0, 0, 0)); /*Opera 11.1-12*/
    background: -moz-linear-gradient(top,rgb(0, 0, 0),rgba(0, 0, 0, 0)); /*Fx 3.6-15*/
    background: linear-gradient(to bottom, rgb(0, 0, 0), rgba(0, 0, 0, 0)); /*Standard*/
    top:0;
    @media screen and (max-width: 768px) {
      content: none;
    }
  }
  &__menu{
    margin:auto;
    justify-content: space-between;
    max-width: 550px;
    padding-top: 10px;
    @media screen and (max-width: 768px) {
      order:2;
      max-width: 100vw;
      background: rgba(0, 0, 0, 0.3);
      text-align: center;
    }
    @media screen and (min-width: 1360px) {
      margin: 0 calc((100vw - 550px)/2);
    }
    ul{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      padding: 0;
      li{
        padding:20px;
        list-style:none;
        @media screen and (max-width: 768px) {
          width: 100%;
          &.latest{
            order:2;
          }
        }
        a:hover{
            color: #7de33a!important;
        }
      }
    }
    a{
      font-size: 30px;
    }
  }
  .content_container{
    @media screen and (max-width: 768px) {
      background: rgba(0, 0, 0, 0.3);
    }
  }
}

.map{
  margin:20px;
  @media screen and (max-width: 768px) {
    order:0;
  }
  iframe{
    min-width: 400px;
    width: 37vw;
    @media(max-width: 768px){
      min-width: auto;
      width: auto;
    }
  }
}
.directions{
  padding: 0 50px;
  max-width: calc( 70% - 400px);
  @media(max-width: 1024px){
    max-width: calc( 70% - 200px);
  }
  @media(max-width: 768px){
    max-width: none;
    padding: 0 30px;
    order: 1;
    p{
      font-size: 20px;
    }
  }
}
.indexmain .footer.tacos{
  @media screen and (max-width: 768px) {
    background-position: bottom;
    background-size: 120vw;
  }
}
