@font-face {
 font-family:'FuturaPT-light';
 src:url(../fonts/FuturaPT-Book.woff) format('woff'),url(../fonts/FuturaPT-Book.ttf) format('truetype');
}
@font-face {
 font-family:'FuturaPT';
 src:url(../fonts/FuturaPT-Light.woff) format('woff'),url(../fonts/FuturaPT-Light.ttf) format('truetype');
}
body{
  display:block;
  margin:0;
  background: #000;
  background: url(/wp-content/themes/veladora/images/fst_scrn_bg.jpg) no-repeat #000;
  background-position-y: top;
  background-size: 100%;
  position: relative;
  // overflow: hidden;
  &.page-menu{
    background: #000;
  }
  @media screen and (max-width: 768px) {
    background-position-x: 50%;
    background-size: 120vh;
  }
  *{
      font-family: FuturaPT-Light;
      -webkit-font-smoothing:antialiased;
      font-weight: normal;
  }
  h1,h2,h3,h4, input,
  .h1,.h2,.h3,.h4{
    display:block;
    color:#fff;
  }
  a{
    text-decoration:none;
    color:#fff;
    text-transform: lowercase;
    // cursor: url('/wp-content/themes/veladora/images/cursor.cur'), pointer;
  }
  p {
    font-size: 24px;
    line-height: 1.4;
    color: #fff;
  }
}
.container{
    display:flex;
    flex-wrap: wrap;
    flex-direction: row;
    @media screen and (max-width: 768px) {
      flex-direction: column;
    }
}
.content_container{
    justify-content: space-between;
    max-width: 1400px;
    margin: 0 auto;
    width:100%;
    z-index: 2;
    @media screen and (max-width: 1024px) {
      flex-wrap: nowrap;
    }
}
.h1{
  font-size: 60px;
  padding-bottom: 20px;
  line-height: 1;
  @media screen and (max-width: 768px) {
    font-size:30px;
  }
}
.h2{
  font-size: 42px;
  @media screen and (max-width: 768px) {
    font-size:30px;
  }
}
.h3, .h3 p{
  font-size: 32px;
  margin: 0;
  @media screen and (max-width: 768px) {
    font-size: 24px;
    line-height: 1.2;
  }
}
::selection {
background: #7de33a; /* Safari */
}
::-moz-selection {
background: #7de33a; /* Firefox */
}
input{
  font-size: 28px;
  border-radius: 0;
  @media screen and (max-width: 768px) {
    font-size: 20px;
  }
}

.active{
  opacity:1!important;
  height: inherit!important;
  // display: block!important;
}

*._mobile {
display: none;
  @media screen and (max-width: 768px) {
    display: block;
    &.container {
      display: flex;
    }
  }
}
*._desktop {
  display: block;
  &.container {
    display: flex;
  }
  @media screen and (max-width: 768px) {
    display: none;
    &.container {
      display: none;
    }
  }
}
.menu_js {
  display:none;
  &.active{
    display: block;
    @media screen and (max-width: 768px) {
      order:2;
    }
  }
}
div.wpcf7-validation-errors,
div.wpcf7-acceptance-missing,
div.wpcf7 .ajax-loader {
  display: none!important;
}
div.wpcf7-mail-sent-ok{
  color: #fff;
  font-size: 24px;
  text-align: center;
}
div.ui-datepicker {
  z-index: 100!important;
}
@import './header';
@import './footer';
@import './index';
@import './menu';
@import './content';
@import './preloader';
